@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
    margin: 0;
    font-family: "Roboto", sans-serif;
}

body {
    background-color: #CACACA;
}

button {
    border: none;
}

.line {
    width: 95%;
    margin: 0px 2.5% 0px 2.5%;
    background-color: black;
    height: 1px;
    margin-top: 10px;
}

header {
    height: 280px;

    .links {
        height: 40px;
        width: 100%;
        margin-top: 30px;
        display: flex;
        justify-content: flex-end;

        .inner {
            width: 860px;
            height: 100%;
            display: flex;
            justify-content: space-evenly;

            button {
                font-size: 21px;
                line-height: 24px;
                letter-spacing: 0.2px;
                height: 40px;
                border-radius: 20px;
                text-transform: uppercase;
                padding: 0 10px 0 10px;

                &:nth-child(1) {
                    background-color: black;
                    border: 1px solid black;
                    color: white;
                    width: 81px;
                    font-size: 19px;
                }
                &:nth-child(1n+2) {
                    width: auto;
                    background-color: transparent;
                    border: 1px solid black;
                    font-weight: 700;
                }
                &:nth-child(5) {
                    background-color: #99FF91;
                    padding-right: 15px;
                }
            }
        }
    }
}

.firstblock {
    width: 1300px;
    height: 619.31px;
    margin: 0 19.5px 0 19.5px;
    display: flex;

    .leftblock {
        width: 55%;
        height: 100%;

        p {
            margin-top: 20px;
           &:nth-child(1) {
            font-size: 24px;
            line-height: 28.8px;
            text-transform: uppercase;
           } 
           &:nth-child(2) {
            font-size: 98px;
            line-height: 98px;
            letter-spacing: 2.8px;
            text-transform: uppercase;
            font-weight: 700;
           }
           &:nth-child(3) {
            font-size: 28px;
            line-height: 33.6px;
           }
           &:nth-child(4) {
            font-size: 18px;
            line-height: 27px;
            width: 569px;
           }
        }

        button {
            border: 1px solid black;
            background-color: #99FF91;
            width: 191px;
            height: 27px;
            border-radius: 100px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 20px;
            line-height: 18px;
            font-weight: 700;
            text-transform: uppercase;
            margin-top: 30px;

            img {
                top: 0;
                margin-left: 5px;
                width: 17.48px;
                height: 17.85px;
            }
        }
    }

    img {
        position: relative;
        bottom: 30px;
    }
}